<template>
    <div class="bg-gray-200 min-h-screen">
        <header class="logo">
            <div class="flex justify-center">
                <img :src="require('../public/marca-vacina-vantagem.png')" alt="" />
            </div>
        </header>
        <div class="container mx-auto">
             <button
                v-if="csv.length > 0"
                @click.prevent="limpa()"
            >
                <img :src="require('../public/voltar.png')" alt="" class="w-4 absolute top-4 left-0 h-8 w-8 ml-9 .." />
            </button>
            <div class="flex justify-center">
                <!--  -->
                <div class="my-10 bg-white shadow-lg rounded-xl p-16 pr-8 pl-8 ">
                    <!-- IMPORTANDO ARQUIVO-->
                    <vue-csv-import
                        v-if="csv.length == 0"
                        v-model="csv"
                        :fields="{
                            name: { required: true, label: 'name' },
                            cpf: { required: true, label: 'cpf' },
                            birthdate:{ required: true, label: 'birthdate'},
                        }"
                    >
                        <vue-csv-input @change="load()"></vue-csv-input>
                        <div v-if="loading === true && csv.length == 0" class="flex justify-center">
                            <img class="mt-4 h-20 w-20" :src="require('../public/load2.gif')" alt="" />
                        </div>
                        <vue-csv-map :auto-match="true"></vue-csv-map>
                    </vue-csv-import>
                    <!--  MOSTRANDO RESULTADO NA TELA DO SORTEIO-->
                    <div v-else>
                        <div class="flex justify-center mb-10">
                            <h3 class="semente">Número de Sorteados:</h3>
                            <!--COLOCANDO O NUMERO DE SORTEADOS-->
                            <input
                                type="number"
                                v-model="numero"
                                class="border-solid border-4 border-light-blue-500 py-2 px-4 rounded"
                            />
                            <h3 class="semente">Resultado da Loteria Federal:</h3>
                             <!--COLOCANDO O NUMERO DA SEMENTE-->
                            <input
                                type="number"
                                v-model="semente"
                                class="border-solid border-4 border-light-blue-500py-2 px-4 rounded"
                            />
                            <button
                                class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-6 rounded"
                                @click.prevent="roda()"
                            >
                                Realizar sorteio
                            </button>
                        </div>
                        <div v-if="l2 === true" class="flex justify-center">
                            <img class="mt-4 h-20 w-20" :src="require('../public/load2.gif')" alt="" />
                        </div>
                        <div class="grid grid-cols-3 gap-4">
                            <div
                                class="shadow bg-blue-200 text-center p-6"
                                v-for="(r, i) in result"
                                :key="i"
                            >
                               
                                <h4 class= "pb-2">{{ csv[r].name }}</h4>
                                <p class= "pb-2">
                                  CPF: ***.{{ csv[r].cpf.substr(3, 3) }}.{{
                                        csv[r].cpf.substr(6, 3)
                                   }}-**
                                </p>
                                <p class= "pb-2">
                                    Aniversário:
                                    {{csv[r].birthdate.substr(0,2)}} de
                                    {{mesParaTexto(csv[r].birthdate.substr(3,2))}}                                 
                                </p>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    data: () => ({
        loading: false,
        semente: "",
        numero: "",
        csv: [],
        result: [],
        l2: false,
        meses: ['janeiro','fevereiro','março','abril','maio','junho',
        'julho','agosto','setembro','outubro','novembro','dezembro']
    }),
    methods: {
        // enquanto carregar os dados vai aparecer um loading
        load(){
            this.loading = true;
        },
        //quando voltar  para o site do sorteio vai ta limpo
        limpa() {
            this.l2 = false;
            this.semente = "";
            this.numero = "";
            this.csv = [];
            this.result = [];
            this.loading = false;
        },
        // rodando o loading
        roda() {
            this.l2 = true;
            setTimeout( ()=> {
                this.sorteio()
            }, 2000);
        },
        //função para chamar meses
        mesParaTexto(mes){
            return this.meses[parseInt(mes)-1]
        },
        sorteio() {
            //pegando a semente da loteria federal - digitada pelo usuario //
            const semente = this.semente;
            //pegando os arquivos do csv
            const inscritos = this.csv.length;
            //setando a semente
            Math.seedrandom(semente);
            //criando duas listas
            const vacinados = new Array(inscritos);
            const resultado = new Array(inscritos);
            //entrando no loop do for
            for (let i = 0; i < inscritos; i++) {
                vacinados[i] = 1 + i;
                resultado[i] = 0;
            }
            //  Gerando numero aleatorio //
            for (let i = 0; i < inscritos; i++) {
                //destribuição uniforme
                let aleatorio = Math.floor(Math.random() * inscritos);
                // +1 para o ultimo da lista 
                while (vacinados[aleatorio] == 0) {
                    aleatorio = (1 + aleatorio) % inscritos;
                }
                resultado[i] = vacinados[aleatorio];
                vacinados[aleatorio] = 0;
                this.result = resultado.slice(0, this.numero);
                this.l2 = false;
            }
        },
    },
};

</script>
